<template>
    <div>
        <TopButtons/>
        <div style="height: 10px;"></div>
        <a-card>
            <CheckComment :comment="comment"/>
            <AuditForm v-model="auditForm" @submit="handleSubmit"/>
        </a-card>
    </div>
</template>

<script>
    import TopButtons from '../留言列表我发起的/components/TopButtons'
    import CheckComment from '@/components/comment/check'
    import {getComment,auditComment} from '@/api/comment'
    import AuditForm from '@/components/checkSuffixForm/audit'

    export default {
        components: {
            TopButtons, CheckComment, AuditForm
        },
        props: {
            id: {
                required: true,
                type: [String, Number]
            },
        },
        computed: {
            thisId() {
                return parseInt(this.id);
            }
        },
        data() {
            return {
                comment: {},
                auditForm: {
                    is_passed: null,
                    reason: null,
                },
            }
        },
        methods: {
            fetch() {
                getComment(this.thisId)
                    .then(res => {
                        this.comment = res.data;
                    })
            },
            handleSubmit() {
                let data = {
                    status: this.auditForm.is_passed ? 1 : 2,
                    reason: this.auditForm.reason
                };
                auditComment(this.id, data)
                    .then(res => {
                        this.$message.success('操作成功');
                    })
            },
        },
        created() {
            this.fetch();
        }
    }
</script>
